import navBar from '@/components/nav-bar.vue'
import tagItem from '@/components/tag.vue'
export default {
	data() {
		return {
			value: '',
			isShow: false,
			hottags: ["母婴", "美妆", "家电", "数码", "羊毛衫","女装","百货","鞋包","男装","运动"],
			tags: [],
		}
	},
	components: {
		navBar,
		tagItem
	},
	onShow() {
		this.tags = uni.getStorageSync('historysearch') || [];
	},
	onLoad() {
		this.tags = uni.getStorageSync('historysearch') || [];
	},
	onNavigationBarSearchInputChanged(e){
		this.getinput(e);
		console.log(e);
	},
	onNavigationBarSearchInputConfirmed(e){
		this.onTap(e.text);
	},
	methods: {
		clearsearch() {
			let that = this;
			if (this.tags.length == 0) return
			uni.showModal({
				title: '温馨提示',
				content: '确定要删除历史搜索记录？',
				success: function(res) {
					if (res.confirm) {
						that.tags = [];
						console.log('清楚后的tags', that.tags)
						uni.setStorageSync('historysearch', that.tags);
						uni.showToast({
							title: '删除成功',
							icon: 'success'
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		submit(e) {
			this.value = e.detail.value.trim();
			this.uptags(this.value);
			uni.navigateTo({
				url: `/pages/common/search-detail?keyword=${this.value}`
			})
		},
		uptags(value) {
			if (this.tags.indexOf(this.value) != -1) {
				return;
			}
			if (this.tags.length == 10) {
				this.tags.pop();
				this.tags.unshift(this.value);
			} else {
				this.tags.unshift(this.value);
			}
			uni.setStorageSync('historysearch', this.tags);
		},
		getinput(e){
			this.value = e.text;
			if (this.value) {
				this.isShow = true
			} else {
				this.isShow = false
			}
		},
		_getinput(e) {
			this.value = e.detail.value.trim();
			if (this.value) {
				this.isShow = true
			} else {
				this.isShow = false
			}
		},
		clear() {
			console.log('清楚');
			this.value = ''
			this.isShow = false;
		},
		onTap(value) {
			console.log('aaac', value)
			this.value = value
			this.isShow = true
			this.submit({
				detail: {
					value,
				}
			})
			// console.log(value)
		},
	}
}
